<template>
  <div class="goodsattribute">
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item label="所属公司" size="mini">
            <el-select clearable v-model="select_option2" placeholder="请选择" filterable @focus="getCompany">
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="仓库类型" size="mini">
            <el-select clearable v-model="select_option" placeholder="请选择" @focus="getWarehouseType">
              <el-option :label="item.warehouse_type_name" :value="item.warehouse_type_id" :key="item.warehouse_type_id" v-for="item in warehouseTypeList.options"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="dialogFormVisible = true" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" v-loading="loading1" style="width: 100%" resizable @selection-change="handleSelectionChange" max-height="600">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column label="操作" fixed="right" width="54">
            <template slot-scope="scope">
              <el-button @click="handleEdit(scope.$index, scope.row)" type="text" size="small">
                修改
              </el-button>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="所属组织">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="warehouse_type_name" width="150" label="名称">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_user_name" width="120" label="创建人">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_time" width="180" label="创建时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" prop="update_user_name" width="120" label="修改人">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="update_time" width="180" label="修改时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" prop="remark" label="备注">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>

      <el-dialog :close-on-click-modal="false" title="仓库类型" @open="open" center :visible.sync="dialogFormVisible" width="450px">
        <el-form :model="ruleForm" ref="ruleForm" class="errorItem errorItemMl121">
          <el-form-item label="名称" prop="warehouse_type_name" :rules="[{ required: true, message: '请输入名称', trigger: 'blur' }]">
            <el-input v-model="ruleForm.warehouse_type_name" placeholder="请输入" maxlength="50"></el-input>
          </el-form-item>
          <el-form-item label="摘要">
            <el-input type="text" v-model="ruleForm.remark" maxlength="300" placeholder="请输入"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogFormsubmit('ruleForm')">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import {
  getCompanyListCommon,
  getDepartmentListCommon
} from "common/utils/public_function/commonUtils";
import { getData } from "common/utils/public_function/commonUtils";
export default {
  name: "systemInfoSalesRepertorytype",
  data() {
    return {
      authority: {},
      companyList: [],
      // 表格数据加载
      TableData: {
        tableData: [],
        total: 10
      },
      AccordingParams: {}, //参数传递值
      goPage: 1, //默认搜索页面
      pageSize: 30, //默认表格size
      loading: true,
      per_page: [30, 50, 100, 500],
      select_option2: "",
      select_option: "", //下拉框选中项
      multipleSelection: [], //已选项
      dialogFormVisible: false,
      ruleForm: {
        warehouse_type_name: "",
        remark: ""
      },
      warehouseTypeList: {
        options: []
      }
    };
  },
  methods: {
    companyChange(val) {
      getDepartmentListCommon(this, this.params, 1);
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "select_option", null);
      this.$set(this, "select_option2", null);
      this.$set(this, "AccordingParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getEntrepotList", //接口名
        vm: this //传递的this
      });
      getData(this.AccordingParams);
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleEdit(index, row) {
      this.dialogFormVisible = true;
      this.$nextTick(function() {
        this.ruleForm = Object.assign({}, row);
      });
    },
    onSearch() {
      //查询
      Object.assign(
        this.AccordingParams.params,
        {
          warehouse_type_id: this.select_option,
          company_id: this.select_option2
        },
        {
          page: 1
        }
      );
      getData(this.AccordingParams);
    },
    handleSizeChange(val) {
      //改变展示的数量
      Object.assign(this.AccordingParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.AccordingParams);
    },
    handleCurrentChange(val) {
      // 跳页码
      Object.assign(this.AccordingParams.params, {
        page: val
      });
      getData(this.AccordingParams);
    },
    deletes() {
      var deledata = {
        deleID: "warehouse_type_id",
        deleSelect: this.multipleSelection,
        deleApi: "delEntrepotType",
        list: {
          UrlApi: "getEntrepotList",
          data: this.TableData
        }
      };
      this.$common.deletes(deledata, "删除", this);
    },
    open() {
      this.ruleForm = {};
    },
    dialogFormsubmit(ruleForm) {
      var This = this;
      this.$refs[ruleForm].validate(valid => {
        if (valid) {
          this.ruleForm.company_id = localStorage.getItem("company_id");
          if (This.ruleForm.warehouse_type_id == undefined) {
            let funcan = {
              eachData: this.ruleForm,
              UrlApi: "addEntrepotType",
              closeDialog: (this.dialogFormVisible = false),
              list: {
                UrlApi: "getEntrepotList",
                data: this.TableData,
                esData: this.AccordingParams.params
              }
            };
            this.$common.addAndupdateFun(funcan);
          } else {
            let funcan = {
              eachData: this.ruleForm,
              UrlApi: "editEntrepotType",
              closeDialog: (this.dialogFormVisible = false),
              list: {
                UrlApi: "getEntrepotList",
                data: this.TableData,
                esData: this.AccordingParams.params
              }
            };
            this.$common.addAndupdateFun(funcan);
          }
        } else {
          return false;
        }
      });
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    getCompany() {
      getCompanyListCommon(this); //获取组织
    },
    getWarehouseType() {
      this.$common.options(
        "getWarehouseTypeListOption",
        this.warehouseTypeList
      );
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$set(this, "AccordingParams", {
      params: {}, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getEntrepotList", //接口名
      vm: this //传递的this
    });
    getData(this.AccordingParams);
  }
};
</script>
